<template>
  <div class="ts">
    <!-- 头部导航 -->
    <div class="header">
      <van-nav-bar title="我的学习" left-arrow @click-left="onClickLeft" />
    </div>
    <!-- 标签页切换 -->
    <van-tabs v-model="active">
      <van-tab v-for="(item,index) of tl" :key="index">
        <div slot="title" @click="onClick(item)">{{ item.name }}</div>
        <!-- loading加载 -->
        <div class="lo" v-show="sh">
          <van-loading size="35" color="#eb5600" />
        </div>
        <div class="tabs" v-show="!sh">
          <van-empty description="还没有课程，快去选择课程学习吧" v-if="list == ''" />
          <ul>
            <li v-for="(v,i) of list" :key="i">
              <h5>
                <p>{{ v.title }}</p>
                <b>共{{ v.today_course_num }}课时</b>
              </h5>
              <h4>
                <van-progress :percentage="v.today_course_num" inactive />
              </h4>
            </li>
          </ul>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: "",
      tl: [
        //切换数据
        { type: 2, name: "直播课" },
        { type: 5, name: "点播课" },
        { type: 8, name: "音频课" },
        { type: 10, name: "图文课" },
        { type: 7, name: "面授课" },
        { type: 11, name: "会员课" }
      ],
      list: [], //每个课程对应的数据
      sh: true
    };
  },
  mounted() {
    // 进入页面默认获取数据
    this.sh = true;
    this.$http.get("/api/app/myStudy/2").then(res => {
      // console.log(res);
      this.list = res.data.data.courseList;
      this.sh = false;
    });
  },
  methods: {
    // 返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    // 我的学习数据
    async onClick(item) {
      this.sh = true;
      let { data: res } = await this.$http.get(`/api/app/myStudy/${item.type}`);
      // console.log(res);
      this.list = res.data.courseList;
      this.sh = false;
    }
  }
};
</script>

<style lang="scss">
.ts {
  .van-tab__text--ellipsis {
    font-size: 30px;
  }
  .van-tabs--line .van-tabs__wrap {
    height: 90px;
  }
  .van-nav-bar__title {
    font-size: 35px;
    color: #666;
  }
  .van-nav-bar {
    height: 100px;
    background: #fff;
  }
  .van-icon-arrow-left::before {
    color: #666;
    font-size: 45px;
  }
  .lo {
    height: 1100px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .van-empty__image {
    width: 300px;
    height: 300px;
  }
  .van-empty__description {
    font-size: 30px;
  }
  .van-empty {
    margin-top: 150px;
  }
  .tabs {
    height: 100%;
    ul {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      li {
        width: 90%;
        height: 200px;
        margin-top: 20px;
        border-radius: 10px;
        padding: 30px 25px 30px 25px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background: #fff;
        box-shadow: 3px 3px 3px #ccc;
        h5 {
          p {
            font-size: 30px;
          }

          b {
            font-size: 25px;
          }
        }
        h4 {
          width: 200px;
          font-size: 25px;
        }
      }
    }
  }
}
</style>