      this.$router.push("/options");
    },
    // 特色课数据
    ts() {
      this.$router.push("/ts");
    },
    // 头部数据
    async header() {
      //  loding加载
      this.sh = true;
      let { data: res } = await this.$http.get("/api/app/getUCenterInfo?");
      // console.log(res);
      if(res.code == 200){
        this.$toast(res.msg);
      }
      this.arr = res.data;
      //loding加载
      this.sh = false;
    },
    // 头像和手机号点击事件
    ren(){
      this.$router.push('/info')
    },
    // 跳转到收藏页面
