<template>
  <div class="courseList">
    <ul>
      <li v-for="(v,i) of courseList" :key="i" @click="course(v.id,v.course_type)">
        <h4>{{ v.title }}</h4>
        <div class="bo">
          <h3 v-for="(m,n) of v.teachers_list" :key="n">
            <div class="ten">
              <img :src="m.teacher_avatar" alt />
              <p>{{ m.teacher_name }}</p>
            </div>
            <img src="../../assets/bl.png" alt class="tuu" v-if="v.has_buy == 1" />
          </h3>
        </div>
        <h2>
          <p>{{ v.sales_num }}人报名</p>
          <b id="pr">{{ v.price | Myprice }}</b>
        </h2>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props:["courseList"],
  methods: {
    // 进入课程详情页
    course(id, course_type) {
      // console.log(id);
      this.$router.push({
        path: "/course",
        query: { id: id, course_type: course_type }
      });
    }
  },
  filters: {
    Myprice(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    }
  }
};
</script>

<style lang="scss">
.courseList {
  ul {
    li {
      width: 100%;
      height: 407px;
      background: #fff;
      border-radius: 10px;
      margin-bottom: 30px;
      padding: 0px 29px 0 29px;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      h4 {
        font-size: 33px;
      }
      .bo {
        display: flex;
        flex-wrap: wrap;
        h3 {
          display: flex;
          width: 100%;
          height: 55px;
          align-items: center;
          margin-bottom: 15px;
          justify-content: space-between;
          .ten {
            display: flex;
            align-items: center;
            img {
              width: 55px;
              height: 55px;
              border-radius: 50%;
            }
            p {
              font-size: 20px;
              color: #666;
              margin: 0 20px;
            }
          }
          .tuu {
            width: 100px;
            height: 100px;
          }
        }
      }
      h2 {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-top: 1px solid #eee;
        padding-top: 26px;
        p {
          font-size: 23px;
          color: #666;
        }
        b {
          font-size: 33px;
          color: #389d1d;
        }
      }
    }
  }
}
</style>