<template>
  <div class="info">
    <!-- 头部导航 -->
    <div class="header">
      <van-nav-bar title="个人信息" left-arrow @click-left="onClickLeft" />
    </div>
    <!-- loding加载 -->
    <div class="lo" v-show="sh">
      <van-loading size="35" color="#eb5600" />
    </div>
    <!-- 列表数据 -->
    <div class="lis" @click="setInfo('avatar')">
      <h5>头像</h5>
      <div class="bin">
        <img :src="arr.avatar" alt />
        <input type="file" accept="image/*" style="display:none;" ref="fileTag" @change="upDataImg" />
        <van-icon name="arrow" size="20" color="#ccc" />
      </div>
    </div>
    <div class="lis" @click="setInfo('nickname')">
      <h5>昵称</h5>
      <div class="bin">
        <p>{{ arr.nickname }}</p>
        <van-icon name="arrow" size="20" color="#ccc" />
      </div>
    </div>
    <div class="lis">
      <h5>手机号</h5>
      <div class="bin">
        <p style="margin-right:30px">{{ arr.mobile }}</p>
      </div>
    </div>
    <div class="lis" @click="setInfo('sex')">
      <h5>性别</h5>
      <div class="bin">
        <p>{{ arr.sex | Se }}</p>
        <van-icon name="arrow" size="20" color="#ccc" />
      </div>
    </div>
    <div class="lis" @click="re = true">
      <h5>出生日期</h5>
      <div class="bin">
        <p>{{ arr.birthday }}</p>
        <van-icon name="arrow" size="20" color="#ccc" />
      </div>
    </div>
    <div class="lis" @click="setInfo('address')">
      <h5>所在城市</h5>
      <div class="bin">
        <p>{{ arr.province_name }}，{{ arr.city_name }}，{{ arr.district_name }}</p>
        <van-icon name="arrow" size="20" color="#ccc" />
      </div>
    </div>
    <div class="lis">
      <h5>学科</h5>
      <div class="bin">
        <p></p>
        <van-icon name="arrow" size="20" color="#ccc" />
      </div>
    </div>
    <div class="lis">
      <h5>年级</h5>
      <div class="bin">
        <p></p>
        <van-icon name="arrow" size="20" color="#ccc" />
      </div>
    </div>
    <!-- 选择出生日期 -->
    <van-popup v-model="re" position="bottom" :style="{ height: '40%'}">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="birth"
      />
    </van-popup>
    <!-- 弹出层 -->
    <van-popup v-model="suo" position="bottom">
      <!-- 头像 -->
      <div v-if="tag == 'avatar'" class="head">
        <ul>
          <li @click="upload">拍照</li>
          <li @click="upload">从手机相册选择</li>
          <li style="border-bottom:0;" @click="cancel">取消</li>
        </ul>
        <!-- <button @click="upload">上传文件</button> -->
      </div>
      <!-- 所在城市 -->
      <van-area
        v-else-if="tag == 'address'"
        :area-list="areaList"
        @confirm="city"
        @change="changeArea"
        :value="arr.district_id+''"
      />
    </van-popup>
  </div>
</template>

<script>
// import address from "../../utils/area";
export default {
  data() {
    return {
      re: false,
      show: false,
      suo: false,
      areaList: {
        province_list: {}, //省数据
        city_list: {}, //市数据
        county_list: {} //区数据
      },
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      arr: [], //渲染个人页面数据
      minDate: new Date(1901, 2, 1),
      maxDate: new Date(2020, 6, 19),
      currentDate: new Date(),
      coco: [],
      rq: "", //出生日期
      sh: true, //loding加载
      tag: "" //显示头像和所在城市的内容
    };
  },
  mounted() {
    // 个人信息
    this.user();
    // 获取学科和年级数据
    this.sm();
  },
  methods: {
// 当上传的input类型是file,选择文件点击确定时触发的事件
    upDataImg(e) {
      this.suo = false; //弹出层隐藏
      let file = e.target.files[0];
      let fromdata = new FormData();
      fromdata.append("file", file);
      // 第一个请求接口，将图片上传至服务器的资源目录
      this.$http.post("/api/app/public/img", fromdata).then(res => {
          // console.log(res)
          // 第二个请求接口，将服务器的资源目录的图片传到user接口
          return this.$http.put("/api/app/user", {avatar: res.data.data.path});
        }).then(res => {
          // console.log(res);
          if (res.data.code == 200) {
            // 重新刷新页面
            this.user();
          } else {
            this.$toast("头像设置失败");
          }
        });
    },
    // 触发上传图片
    upload() {
      this.$refs.fileTag.click();
    },
    // 头像的弹框取消
    cancel(){
      this.suo = false
    },
    // 返回上一页
    onClickLeft() {
      this.$router.push("/user");
    },
    // 个人信息数据
    async user() {
      this.sh = true;
      let { data: res } = await this.$http.get("/api/app/userInfo");
      // console.log(res);
      this.arr = res.data;
      this.sh = false;
    },
    // 点击头像的选项
    event(item) {
      // console.log(item)
    },
    // 点击头像取消按钮
    onCancel() {
      this.$toast("取消");
    },
// 进行设置的方法
    setInfo(tag) {
      this.tag = tag;
      switch (tag) {
        // 头像
        case "avatar":
          this.suo = true;
          break;
        // 昵称
        case "nickname":
          this.$router.push({
            path: "/setInfo",
            query: {
              tag: "nickname",
              value: this.arr.nickname
            }
          });
          break;
        // 性别
        case "sex":
          this.$router.push({
            path: "/setInfo",
            query: {
              tag: "sex",
              value: this.arr.sex
            }
          });
          break;
        // 所在城市
        case "address":
          // 显示弹出层
          this.suo = true;
          // 调用获取省市区城市数据
          this.getAddress();
          break;
      }
    },
// 获取省市区城市数据
    async getAddress() {
  // 省列表数据的获取和处理
      let { data: res } = await this.$http.get("/api/app/sonArea/0");
      // console.log(res);
      let province = res.data; //省列表数据
      let data = {}; //将要处理的数据，初始化为空
      province.forEach(item => {
        //循环省列表
        data[item.id] = item.area_name;
      });
      this.areaList.province_list = data; //给省赋值

  // 市列表数据的获取和处理
      let { data: resCity } = await this.$http.get(
        `/api/app/sonArea/${
          this.arr.province_id ? this.arr.province_id : province[0].id
        }`
      );
      // console.log(resCity);
      let city = resCity.data; //省列表数据
      data = {}; //将要处理的数据，初始化为空
      city.forEach(item => {
        //循环省列表
        data[item.id] = item.area_name;
      });
      this.areaList.city_list = data; //给市赋值

  // 区列表数据的获取和处理
      let { data: resCounty } = await this.$http.get(
        `/api/app/sonArea/${this.arr.city_id ? this.arr.city_id : city[0].id}`
      );
      // console.log(resCounty);
      let county = resCounty.data; //省列表数据
      data = {}; //将要处理的数据，初始化为空
      county.forEach(item => {
        //循环省列表
        data[item.id] = item.area_name;
      });
      this.areaList.county_list = data; //给区赋值
    },
// 当选项发生改变时，进行触发
    changeArea(picker, data, index) {
      switch (index) {
        case 0:
          this.$http
            .get(`/api/app/sonArea/${data[0].code}`)
            .then(res => {
              // console.log(res);
              let city = res.data.data; //省列表数据
              let data = {}; //将要处理的数据，初始化为空
              city.forEach(item => {
                //循环省列表
                data[item.id] = item.area_name;
              });
              this.areaList.city_list = data; //给市赋值
              return this.$http.get(`/api/app/sonArea/${city[0].id}`);
            })
            .then(res => {
              // console.log(res)
              let county = res.data.data; //省列表数据
              let data = {}; //将要处理的数据，初始化为空
              county.forEach(item => {
                //循环省列表
                data[item.id] = item.area_name;
              });
              this.areaList.county_list = data; //给区赋值
            });
          break;
        case 1:
          this.$http.get(`/api/app/sonArea/${data[1].code}`).then(res => {
            // console.log(res)
            let county = res.data.data; //省列表数据
            let data = {}; //将要处理的数据，初始化为空
            county.forEach(item => {
              //循环省列表
              data[item.id] = item.area_name;
            });
            this.areaList.county_list = data; //给区赋值
          });
          break;
      }
    },
// 获取学科和年级数据
    async sm() {
      let { data: res } = await this.$http.get("/api/app/module/attribute/1?");
      //  console.log(res)
    },
//  出生日期事件
    async birth(d) {
      this.rq = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
      let { data: res } = await this.$http.put(
        `/api/app/user?birthday=${this.rq}`
      );
      //  console.log(res)
      // 从新刷新页面
      this.user();
      this.re = false;
    },
// 所在城市事件
    async city(v) {
      let { data: res } = await this.$http.put(
        `/api/app/user?province_id=${v[0].code}&city_id=${v[1].code}&district_id=${v[2].code}`
      );
      // console.log(res);
      // 从新刷新页面
      this.user();
      this.suo = false;
    }
  },
  filters: {
    // 过滤性别
    Se(val) {
      if (val == 0) {
        return "男";
      } else if (val == 1) {
        return "女";
      } else if (val == 3) {
        return "保密";
      }
    }
  }
};
</script>

<style lang="scss">
.info {
  .van-action-sheet__cancel,
  .van-action-sheet__item {
    height: 100px;
  }
  .van-action-sheet__name {
    font-size: 33px;
  }
  .van-action-sheet__cancel {
    font-size: 33px;
  }
  .van-picker__toolbar {
    margin-top: 25px;
  }
  .van-picker__cancel,
  .van-picker__confirm {
    font-size: 30px;
    color: #eb6100;
  }
  .van-icon-arrow::before {
    font-size: 40px;
  }
  .van-cell__title {
    span {
      font-size: 29px;
    }
  }
  .van-ellipsis {
    font-size: 30px;
  }
  .lo {
    height: 1100px;
    position: fixed;
    top: 50%;
    left: 45%;
  }
  .head {
    ul {
      li {
        font-size: 33px;
        height: 100px;
        line-height: 100px;
        border-bottom: 1px solid #eee;
        text-align: center;
      }
    }
  }
  .header {
    margin-bottom: 20px;
  }
  .van-nav-bar {
    height: 100px;
    background: #fff;
  }
  .van-nav-bar__title {
    font-size: 35px;
    color: #666;
  }
  .van-icon-arrow-left::before {
    color: #666;
    font-size: 45px;
  }
  .van-cell__value {
    line-height: 30px;
    padding-right: 20px;
  }
  .lis {
    width: 100%;
    height: 120px;
    background: #fff;
    display: flex;
    border-bottom: 1px solid #eee;
    padding: 10px 20px;
    align-items: center;
    box-sizing: border-box;
    justify-content: space-between;
    h5 {
      font-size: 30px;
      color: #666;
    }
    .bin {
      display: flex;
      align-items: center;
      img {
        width: 80px;
        height: 80px;
        margin-right: 25px;
      }
      p {
        font-size: 28px;
        color: #666;
        margin-right: 25px;
      }
    }
  }

  .co {
    display: flex;
    justify-content: space-between;
  }
}
</style>