<template>
  <div class="setInfo">
    <!-- 头部导航 -->
    <div class="header">
      <van-nav-bar
        title="修改个人信息"
        left-arrow
        right-text="保存"
        @click-left="onClickLeft"
        @click-right="onClickRight"
      />
    </div>
    <div v-if="tag == 'nickname'">
      <input type="text" v-model="value" />
    </div>
    <div v-if="tag == 'sex'">
      <van-radio-group v-model="radio">
        <van-cell-group>
          <van-cell title="男" clickable @click="radio = '0'">
            <template #right-icon>
              <van-radio name="0" />
            </template>
          </van-cell>
          <van-cell title="女" clickable @click="radio = '1'">
            <template #right-icon>
              <van-radio name="1" />
            </template>
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      tag: "",
      radio: ""
    };
  },
  mounted() {
    // 获取传过来的value值
    this.tag = this.$route.query.tag;
    if (this.tag == "nickname") {
      this.value = this.$route.query.value;
    }
  },
  methods: {
    // 返回上一页
    onClickLeft() {
      this.$router.back(-1);
    },
    // 上传value值
    onClickRight() {
      if (this.tag == "nickname") {
        this.$http.put(`/api/app/user?nickname=${this.value}`).then(res => {
          // console.log(res);
          if (res.data.code == 200) {
            this.$router.push("/info");
          } else {
            this.$toast(res.data.msg);
          }
        });
      }
      if (this.tag == "sex") {
        this.$http.put(`/api/app/user?sex=${this.radio}`).then(res => {
          // console.log(res);
          if (res.data.code == 200) {
            this.$router.push("/info");
          } else {
            this.$toast(res.data.msg);
          }
        });
      }
    }
  }
};
</script>

<style lang="scss">
.setInfo {
  .van-cell__title, .van-cell__value{
    height: 100px;
    line-height: 100px;
  }
  .van-cell__title{
    span{
      font-size: 30px;
    }
  }
  .van-nav-bar__title {
    font-size: 35px;
    color: #666;
  }
  .van-nav-bar {
    height: 100px;
    background: #fff;
  }
  .van-icon-arrow-left::before {
    color: #666;
    font-size: 45px;
  }
  .van-nav-bar__text {
    color: #666;
  }
  .header {
    margin-bottom: 20px;
  }
  input {
    width: 100%;
    height: 100px;
    background: #fff;
    border: 0;
    font-size: 30px;
    padding-left: 25px;
    box-sizing: border-box;
    border: 0;
  }
  .active {
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAcCAYAAAAAwr0iAAAAGXRFW…A6jvi9BMT+lFhOanXMDMQ+0EIKVHfsAuLvFPeMBrprBhBgADxzbshcf)
      no-repeat 100%;
  }
}
</style>