<template>
  <div class="collect">
    <div class="header">
      <van-nav-bar title="我的收藏" left-arrow @click-left="onClickLeft" />
    </div>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <div class="Collection">
          <ul>
            <li v-for="(v,i) of list" :key="i" @click="course(v.id,v.course_type)">
              <h4>{{ v.title }}</h4>
              <div class="bo">
                <div class="ten">
                  <img :src="v.teachersAvatar" alt />
                  <p>{{ v.teachers }}</p>
                </div>
                <img src="../../assets/bl.png" alt class="tuu" v-if="v.has_buy == 1" />
              </div>
              <h2>
                <p>{{ v.sales_num }}人报名</p>
                <b id="pr">{{ v.price | Myprice }}</b>
              </h2>
            </li>
          </ul>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false
    };
  },
  mounted() {
    this.$http.get("/api/app/collect?page=1&limit=10&type=1&").then(res => {
      // console.log(res);
      this.list = res.data.data.list;
    });
  },
  methods: {
    // 返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    // 进入课程详情页
    course(id, course_type) {
      // console.log(id);
      this.$router.push({
        path: "/course",
        query: { id: id, course_type: course_type }
      });
    },
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }
          if (this.list.length == this.list.length) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    }
  },
  filters: {
    Myprice(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    }
  }
};
</script>

<style lang="scss">
.collect {
  .van-list__error-text, .van-list__finished-text, .van-list__loading{
    font-size: 25px;
  }
  .van-nav-bar {
    height: 100px;
  }
  .van-nav-bar__title {
    font-size: 35px;
    color: #666;
  }
  .van-icon-arrow-left::before {
    color: #666;
    font-size: 45px;
  }
  .Collection {
    padding: 30px 20px 0 20px;
    ul {
      li {
        width: 100%;
        height: 407px;
        background: #fff;
        border-radius: 10px;
        margin-bottom: 30px;
        padding: 0px 29px 0 29px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        h4 {
          font-size: 33px;
        }
        .bo {
          display: flex;
          flex-wrap: wrap;
          .ten {
            display: flex;
            align-items: center;
            img {
              width: 55px;
              height: 55px;
              border-radius: 50%;
            }
            p {
              font-size: 20px;
              color: #666;
              margin: 0 20px;
            }
          }
          .tuu {
            width: 100px;
            height: 100px;
          }
        }
        h2 {
          display: flex;
          align-items: center;
          justify-content: space-between;
          border-top: 1px solid #eee;
          padding-top: 26px;
          p {
            font-size: 23px;
            color: #666;
          }
          b {
            font-size: 33px;
            color: #389d1d;
          }
        }
      }
    }
  }
}
</style>