<template>
  <div class="search">
    <!-- 头部导航和搜索 -->
    <van-nav-bar left-arrow @click-left="onClickLeft">
      <template #title>
        <van-search v-model="value" shape="round" placeholder="请输入搜索关键词" @keydown.enter="onSearch"  @input="ko" />
      </template>
      <template #right>
        <div @click="onSearch" class="so">搜索</div>
      </template>
    </van-nav-bar>
    <!-- 渲染内容 -->
    <div class="los" v-show="!sh">
      <h5>
        <p>历史记录</p>
        <van-icon name="delete" @click="sc" size="20"/>
      </h5>
      <ul>
        <li v-for="(item,index) of arr" :key="index" @click="loat(item)">{{ item }}</li>
      </ul>
    </div>
    <div class="jing" v-show="sh">
      <!-- 引入列表组件 -->
      <courseList :courseList="list" />
    </div>
  </div>
</template>

<script>
import courseList from "../../views/Le/courseList";
export default {
  data() {
    return {
      value: "",
      list: [],
      sh: false,
      arr: []
    };
  },
  components: {
    courseList
  },
  mounted() {
    // 获取历史记录
    var lo = JSON.parse(localStorage.getItem("kk"));
    if (lo) {
      this.arr = lo;
    }
  },
  methods: {
    // 返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    // 搜索
    async onSearch() {
      let { data: res } = await this.$http.get(
        `/api/app/courseBasis?limit=10&page=1&course_type=0&keywords=${this.value}`
      );
      // console.log(res);
      this.list = res.data.list;
      this.sh = true;
      // 历史记录存储
      let arr = []   // 创建一个空数组
      let his = localStorage.getItem("kk")   // 获取本地存储
      if(his) {   // 如果有本地存储的历史记录
        arr = JSON.parse(his)
      }
      let bool = arr.some((item)=>{
        return item.includes(this.value)   // 在数组中 如果输入框对应其中某一个值得时候就会 返回来 一个true
      })
      // 如果找到了，不往里添加，没找到 就添加当前input框中得关键字 
      if(!bool) {
        if(arr.length>=5) {
          this.arr.pop()
        }
        this.arr.unshift(this.value);
      }
      
      localStorage.setItem("kk", JSON.stringify(this.arr));
    },
    // 搜索框空的是让历史纪录显示
    ko() {
      if (this.value == "") {
        this.sh = false;
        this.list = [];
      }
    },
    //删除
    sc() {
      localStorage.removeItem("kk");
      this.arr = [];
    },
    // 点击历史记录的内容
    loat(item) {
      this.value = item;
    }
  },
  filters: {
    //  过滤时间
    times(value) {
      let time = new Date(value * 1000);
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      if (month < 10) {
        month = "0" + month;
      }
      let day = time.getDate() < 10 ? "0" + time.getDate() : time.getDate();
      let hour = time.getHours() < 10 ? "0" + time.getHours() : time.getHours();
      let minute =
        time.getMinutes() < 10 ? "0" + time.getMinutes() : time.getMinutes();
      let second =
        time.getSeconds() < 10 ? "0" + time.getSeconds() : time.getSeconds();
      return `${month}月-${day}日 ${hour}:${minute}`;
    },
    // 过滤免费和不免费
    Myprice(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    }
  }
};
</script>

<style lang="scss">
.search {
  .van-nav-bar {
    height: 100px;
    background: #fff;
  }
  .van-icon-arrow-left::before {
    color: #666;
    font-size: 45px;
  }
  .van-icon-search::before {
    font-size: 40px;
  }
  .van-search {
    height: 65px;
  }
  .van-field__control {
    height: 65px;
  }
  .search .van-search {
    line-height: 65px;
  }
  .van-search .van-cell {
    line-height: 65px;
  }
  .van-field__body {
    height: 90px;
  }
  .van-field__left-icon {
    line-height: 90px;
  }
  .so {
    font-size: 30px;
  }
  .los {
    padding: 40px 40px 0 40px;
    h5 {
      display: flex;
      justify-content: space-between;
      p {
        font-size: 33px;
        font-weight: bold;
      }
    }
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        font-size: 25px;
        padding: 8px 20px;
        background: #fff;
        margin: 20px 13px 20px 13px;
        border-radius: 20px;
      }
    }
  }
  .jing {
    padding: 0 30px;
    margin-top: 30px;
  }
}
</style>