<template>
  <div class="options">
    <van-nav-bar title="设置" left-arrow @click-left="onClickLeft" />
    <div class="bo">
        <van-button  color=" #ea7a2f" @click="tc">退出登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    // 返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    // 退出登录
    tc(){
        this.$router.push('/sho')
        localStorage.removeItem("token")
    }
  }
};
</script>

<style lang="scss">
.options {
  .van-nav-bar__title {
    font-size: 35px;
    color: #666;
  }
  .van-icon-arrow-left::before {
    color: #666;
    font-size: 45px;
  }
  .van-nav-bar {
    height: 100px;
  }
  .bo{
      margin-top: 20px;
     display: flex;
     justify-content: center;
      .van-button{
          width: 90%;
          height: 80px;
          border-radius: 10px;
      }
  }
}
</style>