import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import One from "@/views/One.vue"
import Leave from "@/views/Leave.vue"
import Detail from "@/components/Detail.vue"
import Gz from "../views/Le/Gz.vue"
import Course from "@/components/Course.vue"
import Search from "@/views/Le/Search.vue"
import Options from "@/views/Le/Options.vue"
import Ts from "@/views/Le/Ts.vue"
import Info from "@/views/Le/Info.vue"
import SetInfo from "@//views/Le/Set-info.vue"
import Collect from "../views/Le/Collect.vue"
// 二级路由
import Sho from "@/components/He/Sho.vue"
import Kc from "@/components/He/Kc.vue"
import Yk from "@/components/He/Yk.vue"
import Lx from "@/components/He/Lx.vue"
import User from "@/components/He/User.vue"
import Simulation from "@/components/SimulationModelTest.vue"
import Kd from "@/views/Kd.vue"
import Qd from "@/views/Kd/Qd.vue"
import Hx from "@/views/Kd/Hx.vue"
import Sw from "@/views/Kd/Sw.vue"
import Jx from "@/views/Kd/Jx.vue"
import Sx from "@/views/Kd/Sx.vue"
import Wl from "@/views/Kd/Wl.vue"
import English from "@/views/Kd/English.vue"
import Loiding from "@/components/Loding.vue"
import Password from "@/components/FindThePassword.vue"
import Reg from "@/components/RegistrationOfBoarding.vue"
import Szpassword from "@/components/Szpassword.vue"
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect:"sho",
    children:[
      {
        path:"sho",
        component:Sho,
        meta:{
          title:'首页'
        }
      },
      {
       path:"kc",
       component:Kc,
       meta:{
        title:'特色课'
      }
      },
      {
        path:"yk",
        component:Yk,
        meta:{
          title:'约课记录'
        }
      },
      {
        path:"lx",
        component:Lx,
        meta:{
          title:'练习'
        }
      },
      {
        path:"user",
        component:User,
        meta:{
          title:'个人中心'
        }
      },
      {
        path:'simulation',
        component:Simulation
      },
      {
        path:'loding',
        component:Loiding
      },
      {
        path:'password',
        component:Password
      },
      {
        path:'reg',
        component:Reg
      },
      {
        path:'szpassword',
        component:Szpassword
      }
    ]
  },
  {
    path:'/kd',
    component:Kd,
    children:[
      {
        path:'qd',
        component:Qd
      },
      {
        path:'hx',
        component:Hx
      },
      {
        path:'sw',
        component:Sw
      },
      {
        path:'jx',
        component:Jx
      },
      {
        path:'sx',
        component:Sx
      },
      {
        path:'wl',
        component:Wl
      },
      {
        path:'english',
        component:English
      }
    ]
  },
  {
    path:'/one',
    component:One,
    meta:{
      title:'一对一辅导'
    }
  },
  {
    path:'/leave',
    component:Leave
  },
  {
    path:'/detail',
    component:Detail,
    meta:{
      title:'讲师详情'
    }
  },
  {
    path:'/gz',
    component:Gz,
    meta:{
      title:'我的关注'
    }
  },
  {
    path:"/course",
    component:Course,
    meta:{
      title:'课程详情'
    }
  },
  {
    path:'/search',
    component:Search
  },
  {
    path:'/options',
    component:Options,
    meta:{
      title:'设置'
    }
  },
  {
    path:'/ts',
    component:Ts,
    meta:{
      title:'我的学习'
    }
  },
  {
    path:'/info',
    component:Info,
    meta:{
      title:'个人信息'
    }
  },
  {
    path:'/setInfo',
    component:SetInfo,
    meta:{
      title:'修改昵称'
    }
  },
  {
    path:'/collect',
    component:Collect,
    meta:{
      title:'我的收藏'
    }
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router
